import React from "react";
import styles from './index.css'
import useComponentStore from "../components/store";
import { CSSProperties } from "react";
import {  GRID_COLUMN_COUNT, GRID_ROW_HEIGHT } from "../constant";
import { useSchemaStore } from "../schema";

const ChildWrapper = props => {
  return props.children
}

const CanvasRoot = props => {
  const gridStyles: CSSProperties = {
    gridTemplateRows: `repeat(auto-fill, ${GRID_ROW_HEIGHT}px)`,
    gridTemplateColumns: `repeat(${GRID_COLUMN_COUNT}, 1fr)`,
    gridAutoRows: `${GRID_ROW_HEIGHT}px`,
  };
  
  const style = useSchemaStore(state => state.schema.style);
  return (
    <div onContextMenu={e => e.preventDefault()} style={{ ...gridStyles, ...style }} className={styles['canvas-root']} id="canvas-root">{props.children}</div>
  )
}

function travisRender(schemaNode) {
  const { type, id, children } = schemaNode;

  let Component: any = null;

  if (type !== 'CanvasRoot') {
    Component = useComponentStore.getState()[type].Component;
  } else {
    Component = CanvasRoot;
  }

  if (Array.isArray(children)) {
    const renderedChildren = children.map(child => <ChildWrapper key={child.id}>{travisRender(child)}</ChildWrapper>);
    return (
      <Component schemaNode={schemaNode}>
        {renderedChildren}
      </Component>
    );
  }
  return <Component schemaNode={schemaNode} />
}

export {
  travisRender
}