import { Sandbox } from ".";

class FunctionSandBox extends Sandbox {
  private handler;
  constructor(options) {
    super(options);
  }

  public init(context: any) {
    const contextKeys = Reflect.ownKeys(context);
    const contextValues = contextKeys.map(key => context[key]);

    const handler = (code: string) => {
      if (this.options.mode === 'expression') {
        code = `return (${code})`;
      }
      const fn = new Function(...(contextKeys as string[]), code);
      return fn(...contextValues);
    }

    this.handler = handler;
  }

  public exec(code: string) {
    if (!this.handler) {
      console.error('sandbox is uninitial');
      return;
    }
      return this.handler(code);
  }

  public safeExec(code: string) {
    if (!this.handler) {
      console.error('sandbox is uninitial');
      return;
    }
    try {
      return this.exec(code);
    } catch (error) {
      console.error('function sandbox error', error);
    }
  }
}

export default FunctionSandBox;